<style lang='scss' scope>
@import '../../assets/scss/Address.scss';
</style>

<template>
    <div class='box-border member-r' id="addressList">
        <div class='order-wrap'>
            <div class='title'>
                
            </div>
            <div class='address-wrap'>
                <div class='address-wrap'>
                    <div class='address-info'> 
                        <center>
                            <font color='red'><b></b></font>
                        </center>
                    </div>
                    <div class='address-add'>
                        <span class='btn' @click='dialogFormVisible = true'></span>
                        <span class='tip'> <b>{{addressCount}}</b>  <b>20</b> </span>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog title='' :visible.sync='dialogFormVisible' :close-on-click-modal='dialogClose' class="address-form">
            <el-form :model='form'>
                <el-form-item label=':' :label-width='formLabelWidth'>
                    <el-input v-model='form.name' auto-complete='off' style="width: 160px;"></el-input>
                </el-form-item>
                <el-form-item label=':' :label-width='formLabelWidth'>
                    <el-input v-model='form.phone' auto-complete='off' style="width: 160px;"></el-input>
                    &nbsp;&nbsp;&nbsp;:&nbsp;&nbsp;&nbsp;
                    <el-input v-model='form.tel' auto-complete='off' style="width: 160px;"></el-input>
                </el-form-item>
                <el-form-item label='  :' :label-width='formLabelWidth'>
                    <v-distpicker :placeholders="placeholders" @selected="onSelected"></v-distpicker>
                    <div class="area-tip"></div>
                </el-form-item>
                <el-form-item label=':' :label-width='formLabelWidth'>
                    <el-input v-model='form.address' auto-complete='off' style="width: 80%;"></el-input>
                </el-form-item>
            </el-form>
            <p class="submit-btn">
                <el-button type='primary' @click='submit'></el-button>
            </p>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data () {
    return {
      addressCount: 0,
      dialogFormVisible: false,
      dialogClose: false,
      formLabelWidth: '100px',
      placeholders: {
        province: '',
        city: '',
        area: ''
      },
      form: {
        name: '',
        phone: '',
        tel: '',
        region: '',
        address: ''
      }
    }
  },
  methods: {
    submit () {
      console.log(this.form)
    },
    onSelected (e) {
      this.form.region = e.province.value + e.city.value + e.area.value
    }
  }
}
</script>


