<style lang='scss' scope>
@import '../../assets/scss/Message.scss';
.el-checkbox + .el-checkbox,
.el-radio + .el-radio {
  margin-left: 10px !important;
}
#alarmBirthday {
  .ignore-month {
    margin-left: 15px;
  }
  .method-tip {
    margin: -5px 0;
    font-size: pxTorem(12px);
    margin-left: 24px;
    a:hover {
      color: #ff6a00;
      text-decoration: underline;
    }
  }
}
</style>

<template>
	<div class='box-border member-r' id='alarmBirthday'>
		<div class='basic-masseage'>
			<div class='member-base-l'>
				<ul class='mb-tab'>
					<li class='active'>
						<router-link to='/'></router-link>
					</li>
					<li>
						<router-link to='/'></router-link>
					</li>
				</ul>
				<el-form :model='birthdayForm' :rules='rules' ref='birthdayForm' label-width='80px' class='basic-form'>
					<el-form-item label=':' prop='name'>
						<el-input v-model='birthdayForm.name' style='width: 200px'></el-input>
					</el-form-item>
					<el-form-item label=':' prop='sex'>
						<el-radio-group v-model='birthdayForm.sex'>
							<el-radio label='0'></el-radio>
							<el-radio label='1'></el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label=':' prop='birthday'>
						<el-radio v-model='birthdayForm.birthdayRadio' label='1'></el-radio>
						<el-radio v-model='birthdayForm.birthdayRadio' label='2'></el-radio>
						<el-checkbox v-model='birthdayForm.ignoreMonth' class='ignore-month'></el-checkbox>
					</el-form-item>
					<el-form-item prop='date'>
						<el-date-picker type='date' placeholder='' v-model='birthdayForm.birthday' style='width: 200px'></el-date-picker>
					</el-form-item>
					<el-form-item label=':'>
						<el-checkbox-group v-model='birthdayForm.date'>
							<el-checkbox v-for='date in dateOptions' :label='date' :key='date'>{{date}}</el-checkbox>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item label=':'>
						<el-checkbox-group v-model='birthdayForm.methods'>
							<div>
								<el-checkbox key='message' label='message'></el-checkbox>
								<p class='method-tip'>
									 <font color='red'>{{phone}}</font> 
									<router-link to='/'></router-link>
								</p>
							</div>
							<div>
								<el-checkbox key='email' label='email'>Email</el-checkbox>
								<p class='method-tip'>
									 <font color='red'>{{email}}</font> 
									<router-link to='/'></router-link>
								</p>
							</div>
							<div>
								<el-checkbox key='weixin' label='weixin'></el-checkbox>
							</div>
							<div>
								<el-checkbox key='remark' label='remark'></el-checkbox>
							</div>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item>
						<el-button type='primary' @click="submitForm('birthdayForm')"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  data () {
    return {
      phone: '129*****234',
      email: '',
      dateOptions: ['', '', '', '', ''],
      birthdayForm: {
        name: '',
        sex: '0',
        birthdayRadio: '1',
        ignoreMonth: true,
        birthday: '',
        date: ['', '', ''],
        methods: ['message', 'email']
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: ' 1 ', trigger: 'blur' }
        ],
        sex: [{ required: true, message: '', trigger: 'change' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        birthday: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log('submit!')
        } else {
          console.log('error submit!!')
        }
      })
    }
  }
}
</script>



