<style lang='scss' scope>
	@import '../../assets/scss/Message.scss';
</style>

<template>
    <div class='box-border member-r'>
			<div class='basic-masseage'>
				<div class='member-base-l'>
					<ul class='mb-tab'>
						<li class='active'>
							<a href='#'></a>
						</li>
						<li>
							<a href='#'></a>
						</li>
					</ul>
					<el-form :model='ruleForm' :rules='rules' ref='ruleForm' label-width='80px' class='basic-form'>
						<el-form-item label=':' prop='name'>
							<el-input v-model='ruleForm.name' style="width: 200px;"></el-input>
						</el-form-item>
						<el-form-item label=':' prop='sex'>
							<el-radio-group v-model='ruleForm.sex'>
								<el-radio label='0'></el-radio>
								<el-radio label='1'></el-radio>
								<el-radio label='2'></el-radio>
							</el-radio-group>
						</el-form-item>
						<el-form-item label=':'>
							<el-col :span='12' style="padding: 0px;">
								<el-form-item prop='date'>
									<el-date-picker type='date' placeholder='' v-model='ruleForm.date' style='width: 100%;'></el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span='12' class="surprice">
								
							</el-col>
						</el-form-item>
						<el-form-item label=':'>
							qq_2655947@u.hua.com
						</el-form-item>
						<el-form-item label=':'>
							123****2321
						</el-form-item>
						<el-form-item label=':' prop='tel'>
							<el-input type='input' v-model='ruleForm.tel' style="width: 200px;"></el-input>
						</el-form-item>
						<el-form-item label=':'>
              <v-distpicker v-model='ruleForm.region' :placeholders="placeholders" @selected="onSelected" style="margin-left: -14px;"></v-distpicker>
						</el-form-item>
						<el-form-item>
							<el-input type='input' v-model='ruleForm.address' style="width: 400px;"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type='primary' @click="submitForm('ruleForm')"></el-button>
						</el-form-item>
					</el-form>
				</div>
				<div class='member-base-r'>
					<div class='member-info'>
						<div class='user'>
							<div class='avatar'>
								<a href='#'>
									<img src='//img02.hua.com/pc/assets/img/avatar_default_09.jpg' width='92' height='92' alt=''>
								</a>
							</div>
							<p>
								<a href='#'>qq_2655947@u.hua.com</a><br>
								<i class='ico-member-rank'></i>
								<br>0
							</p>
						</div>
					</div>
				</div>
			</div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      placeholders: {
        province: '',
        city: '',
        area: ''
      },
      ruleForm: {
        name: '',
        sex: '0',
        region: '',
        address: '',
        date: '',
        phone: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: ' 1 ', trigger: 'blur' }
        ],
        sex: [{ required: true, message: '', trigger: 'change' }],
        phone: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
        } else {
          console.log('error submit!!')
        }
      })
    },
    onSelected (e) {
      this.ruleForm.region = e.province.value + e.city.value + e.area.value
    }
  }
}
</script>



