<template>
  <div class="box-border member-l">
    <div class="hd"><b></b></div>
    <ul class="bd">
      <li v-for="(i, index) in sideList" :key="index">
        <router-link :to="i.url" :class="[i.url === currentPath ? 'active' : '']">
          <i :class="'ico-' + i.type"></i>{{i.name}}
        </router-link>
        <ul>
          <li v-for="(sub, index) in i.subList" :key="index">
            <router-link :to="sub.url" :class="[sub.url === currentPath ? 'active' : '']">
              {{sub.name}}
            </router-link>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      currentPath: String
    },
    data () {
      return {
        sideList: [{
          name: '',
          type: 'member',
          url: '/member/order',
          subList: []
        }, {
          name: '',
          type: 'order',
          url: '',
          subList: [{
            name: '',
            url: ''
          }, {
            name: '',
            url: ''
          }, {
            name: '',
            url: '/member/comment'
          }, {
            name: '',
            url: '/member/favorite'
          }]
        }, {
          name: '',
          type: 'jifeng',
          url: '',
          subList: [{
            name: '',
            url: '/member/integration'
          }, {
            name: '',
            url: ''
          }]
        }, {
          name: '',
          type: 'accout',
          url: '',
          subList: [{
            name: '',
            url: '/member/message'
          }, {
            name: '',
            url: '/member/address'
          }, {
            name: '',
            url: ''
          }, {
            name: '',
            url: ''
          }, {
            name: '/',
            url: '/member/birthday'
          }, {
            name: 'QQ/',
            url: ''
          }]
        }, {
          name: '',
          type: 'pay',
          url: '/',
          subList: [{
            name: '',
            url: '/member/account'
          }, {
            name: '',
            url: '/'
          }, {
            name: '',
            url: '/'
          }]
        }, {
          name: '',
          type: 'serve',
          url: '/',
          subList: [{
            name: '',
            url: '/'
          }, {
            name: '',
            url: '/'
          }, {
            name: '',
            url: '/'
          }]
        }]
      }
    }
  }
</script>



