    <div class="member-panel">
      <div class="hd">
        <router-link to="/"> &gt;&gt; </router-link>
        <h4></h4>
      </div>
      <div class="bd">
        <p align="center"></p><center><font color="red"><b>,</b></font></center><p></p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        avatar: '//img02.hua.com/pc/assets/img/avatar_default_09.jpg',
        user: '12321312213',
        phone: '123****213',
        member: '',
        orders: [{
          url: '/',
          name: '',
          type: 'pay',
          count: '0'
        }, {
          url: '/',
          name: '',
          type: 'receive',
          count: '0'
        }, {
          url: '/',
          name: '',
          type: 'comment',
          count: '0'
        }],
        coupon: [{
          url: '/',
          name: '',
          count: '0'
        }, {
          url: '/',
          name: '',
          count: '0'
        }, {
          url: '/',
          name: '',
          count: '0'
        }]
      }
    },
    methods: {
    }
  }
</script>



